.note {
  width: 200px;
  border-radius: 15px;
  overflow: hidden;
  position: absolute;
  transition: 0.3s all ease-in-out;
}
.note:hover {
  transform: unset !important;
  z-index: 999;
}
.options_container {
  background-color: var(--sky-blue);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid black;
  margin: 0;
}
.note_btn {
  background-color: white;
  padding: 10px;
  outline: none;
  border: none;
  border-right: 1px solid black;
  width: 25%;
}
.note_btn:last-child {
  border-right: unset;
}
.note_btn_logo {
  fill: var(--sky-blue);
}
.title_input {
  width: 100%;
  vertical-align: top;
  outline: none;
  border: none;
  border-bottom: 1px solid black;
  padding: 12px;
  background-color: transparent;
  color: black;
  text-transform: capitalize;
}

.body_input {
  width: 100%;
  height: 100%;
  resize: none;
  outline: none;
  border: none;
  padding: 12px;
  background-color: transparent;
  color: black;
  text-transform: capitalize;
}
.NotValid {
  padding: 10px;
  text-transform: capitalize;
  color: red;
  font-weight: bolder;
  font-size: 10px;
  visibility: hidden;
}
